.main {
  max-width: 1440px;
  width: 100vw;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
  min-height: 100vh;
  background: #FFFFFF;
  margin: 0 auto 0;
}
.main h3 {
  margin: 5% 0;
}
.main .productsContainer {
  max-width: 1128px;
  width: 100%;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
  height: 100%;
  display: flex;
  flex-flow: wrap column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 0 10px;
}
.main .productsContainer .productItem {
  width: 100%;
  height: 100%;
  border: 2px solid rgba(179, 163, 161, 0.4745098039);
  margin-bottom: 48px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: start;
  padding: 5% 0;
}
.main .productsContainer .productItem .img {
  position: relative;
  max-width: 100%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  min-height: 264px;
  height: 464px;
  margin: 0 14px;
}
.main .productsContainer .productItem .img img {
  width: 100%;
  height: 100%;
  -o-object-fit: contain;
     object-fit: contain;
}
.main .productsContainer .productItem .productInfo {
  max-width: 50%;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  text-align: initial;
  min-height: 264px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: right;
}
.main .productsContainer .productItem .productInfo h5 {
  display: block;
  font-weight: 400;
  font-size: 19px;
  letter-spacing: 0.1px;
  color: #494A4A;
  margin: 20% 0;
  text-align: right;
}
.main .productsContainer .productItem .productInfo .productDesc {
  font-size: 17px;
  margin-bottom: 5%;
}
.main .productsContainer .productItem .productInfo .productPrice {
  display: block;
  font-size: 20px;
  font-weight: 600;
  color: inherit;
  margin-bottom: 10%;
}
.main .productsContainer .productItem .buttons {
  color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 5px 5px;
}
.main .productsContainer .productItem .buttons .button {
  border: 0;
  width: 201px;
  height: 44px;
  color: #494A4A;
  border-radius: 32px;
  width: 60%;
  background: #FFE0DB;
  transition: background-color 0.5s ease-out;
}
.main .productsContainer .productItem .buttons .button:hover {
  color: #FFE0DB;
  background: rgba(73, 74, 74, 0.3411764706);
  cursor: pointer;
}
.main .productsContainer .productItem .buttons .fav {
  cursor: pointer;
}
.main .productsContainer .productItem .buttons .fav img {
  width: 150%;
}
.main .productsContainer .productItem .buttons .minus, .main .productsContainer .productItem .buttons .plus {
  width: 20px;
  height: 20px;
  border: 1px solid #fff;
  border-radius: 30px;
  color: #fff;
  font-size: 23px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}/*# sourceMappingURL=Product.module.css.map */