.productItem {
    max-width: 1130px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-height: 264px;
    height: 100%;
    margin-bottom: 10%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: start;
  }
 .productItem .img {
    position: relative;
    max-width: 100%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    min-height: 264px;
    height: 264px;
    margin: 0 14px;
  }
.productItem .img .productCat {
    position: absolute;
    background: #B3A3A1;
    z-index: 5;
    border-radius: 0px 0px 0px 16px;
    font-weight: 500;
    font-size: 14px;
    color: #fff;
    min-width: 105px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    right: -14px;
    top: 0px;
  }
.productItem .img img {
    width: 100%;
    height: 100%;
    -o-object-fit: contain;
       object-fit: contain;
  }
.productItem .productInfo {
    border-left: 1px solid #cec2c1;
    padding-left: 5px;
    max-width: 264px;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: initial;
    min-height: 264px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
.productItem .productInfo h5 {
    display: block;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 0.1px;
    color: #5e6060;
    margin: 0;
  }
.productItem .productInfo h5:hover {
    color: rgb(104, 21, 21);
  }
.productItem .productInfo .productDesc {
    font-size: 8px;
  }
 .productItem .productInfo .productPrice {
    display: block;
    font-weight: 600;
    color: inherit;
  }
.productItem .productInfo .buttons {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
 .productItem .productInfo .buttons .button {
    border: 0;
    width: 201px;
    height: 44px;
    color: #494A4A;
    border-radius: 32px;
    width: 150px;
    background: #FFE0DB;
    transition: background-color 0.5s ease-out;
  }
.productItem .productInfo .buttons .button:hover {
    color: #FFE0DB;
    background: rgba(73, 74, 74, 0.3411764706);
    cursor: pointer;
  }