.main{
    max-width: 1440px;
    width: 100vw;
    max-height: fit-content;
    min-height: 100vh; ///
    background: #FFFFFF;
    margin: 0 auto 0;
    h3{
        margin: 5% 0;
    }
    .productsContainer{
            max-width: 1128px;
            width: 100%;
            max-height: fit-content;
            height: 100%;
            display: flex;
            flex-flow: wrap column;
            justify-content: center;
            align-items: center;
            margin: 0 auto;
            padding: 0 10px;
        .productItem{

            width: 100%; 
            height: 100%;
            border: 2px solid #b3a3a179;
            margin-bottom: 48px;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: start;
            padding: 5% 0;
            .img{
                position: relative;
                max-width: 100%;
                width: fit-content;
                min-height: 264px;
                height: 464px;
                margin: 0 14px;
            img{
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
            }

            .productInfo{
            max-width: 50%;
            width: 100%;
            height: fit-content;
            text-align: initial;
            min-height: 264px; 
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            text-align: right;
            h5{
                display: block;
                font-weight: 400;
                font-size: 19px;
                letter-spacing: 0.1px;
                color: #494A4A;
                margin: 20% 0;
                text-align: right;

            }
            .productDesc{
                font-size: 17px;
                margin-bottom: 5%;


            }
            .productPrice{
                display: block;
                font-size: 20px;
                font-weight: 600;
                color: inherit;
                margin-bottom: 10%;
            }
            }
            .buttons{

            color: #fff;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            padding: 5px 5px;
            .button {
                border: 0;
                width: 201px;
                height: 44px;
                color: #494A4A;
                border-radius: 32px;
                width: 60%;
                background: #FFE0DB;
                -webkit-transition: background-color 0.5s ease-out;
                -moz-transition: background-color 0.5s ease-out;
                -o-transition: background-color 0.5s ease-out;
                transition: background-color 0.5s ease-out;
                &:hover{
                    color: #FFE0DB;
                    background: #494a4a57;
                    cursor: pointer;
                }
            }
            .fav{
                cursor: pointer;
                img {
                    width: 150%;
                }
            }

            .minus, .plus{
                width: 20px;
                height: 20px;
                border: 1px solid #fff;
                border-radius: 30px;
                color: #fff;
                font-size: 23px;
                display: flex;
                justify-content: center;
                align-items: center;
                cursor: pointer;
            }
        }
        }
        
    }
}