.main {
    max-width: 1440px;
    width: 100vw;
    max-height: fit-content;
    min-height: 100vh;
    background: #B3A3A1;
    margin: 0 auto 0;
    
    .container {
    width: 1200px;
    min-height: 100vh;
    height: 100%;
    margin: 0 auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: nowrap column;
        h3{
            font-size: 32px;
            line-height: 40px;
            text-align: center;
            color: #ffff;
        }
        .cat{
            width: fit-content;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            padding-bottom: 64px;

            .block {
                padding: 0;
                width:fit-content;
                list-style: none;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 14px;
                line-height: 20px;
                text-align: center;
                letter-spacing: 0.1px;
                color: rgba(179, 163, 161, 0.75);
                    li:not(:last-child){
                        margin-right: 24px;
                    }

                    .active{
                        font-weight: 600;
                        color: rgba(126, 102, 98, 0.678);
                    } 

                }
            }

            .window{
                height: 100%;
                max-width: 860px;
                overflow: hidden;
            }


            .cartContainer{
                position: relative;
                background: #B3A3A1;
                display: flex;
                padding: 128px 158px;
                max-width: 1440px;
                width: 100%;
                justify-content: space-between;
                align-items: center;
                padding: 0;
                
                
                
                .arrow{
                    position: absolute;
                    top: 42%;
                    right: 25%;
                    z-index: 6;
                    cursor: pointer;
                }
                .arrowRight{
                    position: absolute;
                    left: -2.5%;
                    top: 42%;
                    z-index: 6;
                    cursor: pointer; 
                }

                .itemsContainer{
                    display: flex;
                    max-width: 867px;
                    width: 100%;
                    align-items: center;
                    // overflow: scroll;
                    margin-right: 20px;
                    //transform: translateX(-32%);
                    transition: translate;
                    transition-property: transorm;
                    transition-duration: 300ms;
                    -webkit-transition: 0.5s ease-out;
                    -moz-transition:  0.5s ease-out;
                    -o-transition:  0.5s ease-out;
                    transition:  0.5s ease-out;

                    .cartItem{
                        max-width: 264px;
                        width: -moz-fit-content;
                        width: fit-content;
                        height: 380px;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-around;
                      
                        &:not(:last-child){
                            margin-right: 24px;
                        }

                        h6{
                            margin: 4px;
                        }
                        .img{
                            height: 264px;
                            width: 264px;
                            background-color: #fff;
                            img{
                                width: 100%;
                                height: 100%;
                                object-fit: contain;
                            }
                        }
                        .buttons {
                            color: #fff;
                            display: flex;
                            flex-direction: row;
                            justify-content: space-between;
                            align-items: center;
    
                            .fav {
                                margin-right: 60px;
                                cursor: pointer;
                            }
    
                            .remove {
                                margin-left: 60px;
                                cursor: pointer;
                            }
                            
                            .minus, .plus {
                                width: 20px;
                                height: 20px;
                                border: 1px solid #fff;
                                border-radius: 30px;
                                color: #fff;
                                font-size: 23px;
                                line-height: 0.6;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                cursor: pointer;
                            }
                        }
                    }

                }
            }


            .orderContainer {
                min-height: 332px;
                width: 264px;
                //border: 2px solid yellow;
                color: #fff;
                height: fit-content;
                h3{
                    color: #fff;
                }

                
                .button {
                    border: 0;
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    align-items: flex-start;
                    padding: 16px 76px;
                    width: 227px;
                    height: 52px;
                    background: #FFE0DB;
                    border-radius: 32px;
                    margin: 32px auto;
                    -webkit-transition: background-color 0.5s ease-out;
                    -moz-transition: background-color 0.5s ease-out;
                    -o-transition: background-color 0.5s ease-out;
                    transition: background-color 0.5s ease-out;
                    &:hover{
                        color: #FFE0DB;
                        background: #494a4a57;
                        cursor: pointer;
                    }
                }
                .orderItem {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    margin-bottom: 32px;
                    .orderTitle{
                        font-size: 12px;
                        max-width: 168px;
                        width: 100%;
                        text-align: initial;
                    }

                }
            }

            // .recommendationsContainer {
            //     width: 1440px;
            //     height: 815px;
            //     background-color: #fff;
            // }

    }
}
