.main {
    max-width: 1440px;
    width: 100vw;
    min-height: 50vh;
    max-height: fit-content;
    min-height: 100vh;
    background: #B3A3A1;
    margin: 0 auto 0;
    h3{
        margin: 5% 0;
    }
    
    .container {
    width: 1200px;
    min-height: 100vh;
    height: 100%;
    margin: 0 auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: wrap column;
        h3{
            font-size: 32px;
            line-height: 40px;
            text-align: center;
            color: #ffff;
        }
        .cat{
            width: fit-content;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            padding-bottom: 64px;

            .block {
                padding: 0;
                width:fit-content;
                list-style: none;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 14px;
                line-height: 20px;
                text-align: center;
                letter-spacing: 0.1px;
                color: rgba(179, 163, 161, 0.75);
                    li:not(:last-child){
                        margin-right: 24px;
                    }

                    .active{
                        font-weight: 600;
                        color: rgba(126, 102, 98, 0.678);
                    } 

                }
            }

            .window{
                height: 100%;
                max-width: 860px;
                overflow: hidden;
            }



            .cartContainer{
                position: relative;
                background: #B3A3A1;
                display: flex;
                padding: 128px 158px;
                max-width: 1440px;
                justify-content: center;
                align-items: center;
                padding: 100px 0;
                .arrow{
                    position: absolute;
                    top: 36%;
                    right: -3%;
                    z-index: 6;
                    cursor: pointer;
                }
                .arrowRight{
                    position: absolute;
                    left: -3.5%;
                    top: 36%;
                    z-index: 6;
                    cursor: pointer; 

                }

                .itemsContainer{
                    display: flex;
                    max-width: 867px;
                    width: 100%;
                    align-items: center;
                    //overflow: scroll;
                    margin-right: 20px;
                    transition: translate;
                    transition-property: transorm;
                    transition-duration: 300ms;
                    -webkit-transition: 0.5s ease-out;
                    -moz-transition:  0.5s ease-out;
                    -o-transition:  0.5s ease-out;
                    transition:  0.5s ease-out;

                    .cartItem{
                        max-width: 264px;
                        width: -moz-fit-content;
                        width: fit-content;
                        height: 400px;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-around;
                      
                        &:not(:last-child){
                            margin-right: 24px;
                        }
                        .img{
                            height: 264px;
                            width: 264px;
                            background-color: #fff;
                            img{
                                width: 100%;
                                height: 100%;
                                object-fit: contain;
                            }
                        }
                        .buttons {
                            color: #fff;
                            display: flex;
                            flex-direction: row;
                            justify-content: space-between;
                            align-items: center;
    
                            .fav {
                                margin-right: 60px;
                                cursor: pointer;
                            }
    
                            .remove {
                                margin-left: 60px;
                                cursor: pointer;
                            }
                            
                            .minus, .plus {
                                width: 20px;
                                height: 20px;
                                border: 1px solid #fff;
                                border-radius: 30px;
                                color: #fff;
                                font-size: 23px;
                                line-height: 0.6;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                cursor: pointer;
                            }
                        }
                    }

                }  
            }



            .recWindow{
                height: 100%;
                max-width: 1440px;
                overflow: hidden;
            }
            

            .recommendContainer {
                position: relative;
                background-color: #fff;

                //padding: 128px 158px;
                max-width: 1181px;
                justify-content: center;
                align-items: center;
                padding: 100px 0;
                box-shadow: 0 0 0 100vmax #fff;
                clip-path: inset(0 -100vmax);
                .arrow{
                    position: absolute;
                    right: 26%;
                    z-index: 6;
                    top: 22%;
                    cursor: pointer;
                }
                .arrowRight{
                    position: absolute;
                    left: 69%;
                    z-index: 6;
                    top: 22%;
                    cursor: pointer; 
                }
                h3{
                    font-weight: 500;
                    font-size: 32px;
                    line-height: 40px;
                    color: #B3A3A1;
                    padding: 30px 0;
                    text-align: left;
                }

                .recommendItems{
                    display: flex;
                    //max-width: 867px;
                    width: 100%;
                    align-items: center;
                    //overflow: scroll;
                    margin-right: 20px;
                    min-height: 450px;
                    transition: translate;
                    transition-property: transorm;
                    transition-duration: 300ms;
                    -webkit-transition: 0.5s ease-out;
                    -moz-transition:  0.5s ease-out;
                    -o-transition:  0.5s ease-out;
                    transition:  0.5s ease-out;
                    .cartItem{
                        max-width: 264px;
                        width: -moz-fit-content;
                        width: fit-content;
                        //max-height: 264px;
                        height: 100%;
                        //margin-bottom: 48px;
                        height: 380px;
                        //border: 2px solid green;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-around;
                      
                        &:not(:last-child){
                            margin-right: 24px;
                        }
                        .img{
                            height: 264px;
                            width: 264px;
                            //border: 2px solid violet;
                            background-color: #fff;
                            img{
                                width: 100%;
                                height: 100%;
                                object-fit: contain;
                            }
                    
                        }
                        .buttons {
                            color: rgb(213, 184, 180);
                            display: flex;
                            flex-direction: row;
                            justify-content: space-between;
                            align-items: center;
                            .fav {
                                margin-right: 60px;
                                cursor: pointer;
                            }
                    
                            .remove {
                                margin-left: 60px;
                                cursor: pointer;
                            }
                            
                            .minus, .plus {
                                width: 20px;
                                height: 20px;
                                border: 1px solid rgb(213, 184, 180);
                                border-radius: 30px;
                                color: rgb(213, 184, 180);
                                font-size: 23px;
                                line-height: 0.6;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                cursor: pointer;
                            }
                        }
                    }
                    
                }
            }

    }
}

