
.main {
    max-width: 1440px;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    .homeContainer{
        width: 100%;
        margin: 0 auto;
        .promo {  
            position: relative;  
            background: #B3A3A1;
            max-width: 1440px;
            width: 100%;
            height: 800px;
            font-family: 'Manrope';
            font-style: normal;
            font-weight: 500;
            font-size: 32px;
            line-height: 40px;
            display: flex;
            .promo1{
                width: 322.6px;
                height: 324.03px;
                left: 23%;
                top: 15%;
                position: absolute;
            }
            .promo2{
                position: absolute;
                width: 436.58px;
                height: 436.58px;
                left: 7%;
                top: 30%;                 
                z-index: 5;
            }
            .content {
                width: 34%;
                text-align: left;
                color: #FCFFF5;
                margin: 18% 5% 10% auto;
                z-index: 20;
                h3{
                    font-weight: 500;
                    font-size: 106%;
                    line-height: 110%;
                    margin-bottom: 1.5rem;
                }
                p{
                    font-weight: 300;
                    font-size: 16px;
                    line-height: 24px;
                    display: flex;
                    align-items: center;
                    letter-spacing: 0.15px;
                    margin-bottom: 10%;
                }
                a{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    text-decoration: none;
                    .button {
                    border: 0;
                    width: 33%;
                    height: 52px;
                    color: #494A4A;
                    border-radius: 32px;
                    width: 150px;
                    background: #FFE0DB;
                    -webkit-transition: background-color 0.5s ease-out;
                    -moz-transition: background-color 0.5s ease-out;
                    -o-transition: background-color 0.5s ease-out;
                    transition: background-color 0.5s ease-out;
                    &:hover{
                        color: #FFE0DB;
                        background: #494a4a57;
                        cursor: pointer;
                    }
                    }
                }
            
            }
        }

    .bestsellers {
        max-width: 1440px;
        width: 100%;
        max-height: 890px;
        height: 100%;
        background: #FFFFFF;
        display: flex;
        flex-flow: wrap;
        justify-content: center;
        align-items: center;
        
        .bestsellers_article {
            position: relative;
            max-width: 354px;
            width: 100%;
            max-height: 510px;
            height: 100%;
            text-align: left;
            margin: 34px;
            h4 {
                font-weight: 500;
                font-size: 178%;
                line-height: 40px;
                color: #B3A3A1;
                margin: 0 0 10%;
            }
            p{                
                color: #494A4A;
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;
                letter-spacing: 0.15px;
            }
            .slider {
                margin-top: 20px;
                display: flex;
                flex-direction: column;
                align-items:center;
                padding: 32px;
                max-width: 264px;
                width: 100%;
                height: 256px;
                background: #FFFFFF;
                box-shadow: 0px 4px 12px 2px rgba(0, 0, 0, 0.08);
                border-radius: 16px;
                h5{
                    text-align: center;
                    font-size: 0.8em;
                    line-height: 1em;
                    letter-spacing: 0.15px;
                    color: #494A4A;
                }
                .slider_content{
                    padding: 10%;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    max-width: 100%;
                    height: 100px;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 16px;
                    letter-spacing: 0.4px;
                    color: #494A4A;
                    -webkit-transition: 0.5s ease-out;
                    -moz-transition:  0.5s ease-out;
                    -o-transition: 0.5s ease-out;
                    transition: 0.5s ease-out;
                    .itemImg{
                        width: 35%;
                    }
                    img{
                        width: 100%;
                    }
                }
                a{
                    font-weight: 500;
                    font-size: 14px;
                    line-height: 20px;
                    letter-spacing: 0.1px;
                    color: #B3A3A1;
                    text-align: center;
                    display: block;
                    padding-top: 10px;
                    &:hover{
                        color: #765752;
                    }
                }
            }

            .arrow {
                position: absolute;
                height: 23.999982833862305px;
                width: 12.961673736572266px;
                right: 2.2%;
                top: 69%;
                    img{
                        width: 100%;
                    }

            }
        }
        .bestsellersPhoto{
            position: relative;
            max-width: 744px;
            width: 100%;
            max-height: 515.75px;
            height: 100%;
            object-fit: contain;
            img{
                width: 100%;

            }
            div{
                position: absolute;
                max-width: 16px;
                width: 100%;
                max-height: 16px;
                height: 100%;
                opacity: 0.8;
                box-shadow: 0px 4px 4px 2px rgba(0, 0, 0, 0.12);
                border-radius: 50%;
                background: #FFE0DB;
                -webkit-transition: background-color 0.5s ease-out;
                -moz-transition: background-color 0.5s ease-out;
                -o-transition: background-color 0.5s ease-out;
                transition: background-color 0.5s ease-out;
                &:hover{
                    background: #B3A3A1;
                }
                &:active{
                    background: #9b7f7b;
                }
            }

            .itemButton1{
                left: 14%;
                top: 26%;
            }

            .itemButton2{
                left: 32%;
                top: 28%;
            }

            .itemButton3{
                left: 34%;
                top: 67%;
            }

            .itemButton4{
                left: 19%;
                top: 74%;
            }

            .itemButton5{
                left: 92%;
                top: 64%;
            }
        }
    }

    .articles {
        max-width: 1440px;
        width: 100%;
        max-height: 900px;
        height: 100%;
        position: relative;
        margin-top: 200px;  
        .articleImg{
 
            max-width: 1440px;
                // img{
                // }
        }
        .articleBlock{
            z-index: 15;
            position: absolute;
            max-width: 708px;
            max-height: 280px;
            right: 0%;
            top: 30%;
            background: #B3A3A1;
            opacity: 0.95;
            -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
            border-radius: 24px 0px 0px 24px;
            width: 100%;
            height: 100%;
            .textBlock{
                padding: 8% 5%;
                max-width: 488px;
                max-height: 148px;
                text-align: left;
                 h4{
                font-weight: 500;
                padding-bottom: 5%;
                font-size: 22px;
                line-height: 28px;
                color: #FFFFFF;
                }
                p{
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;
                letter-spacing: 0.15px;
                color: #FFFFFF;
                padding-bottom: 5%;
                }
                a{
                text-decoration: none;
                color: #fff;
                    img{
                        width: 32px;
                        padding-left: 30px;
                    }
                }
            
            }


        }
    
        } 
    }
}


@media screen and (max-width: 750px) {
    
.main {
    .homeContainer{
        .articles{
            margin-top: 200px;
        }
    }
    }
}


